<template>

  <div id="HomeComp">

  <div id="start_page_container">

    <section id="page_start_section_fixed_1"></section>

    <section id="start_page_section_1" v-on:scroll.passive="showSection_1">
      <transition name="slide-fade-left">
        <div id="start_page_section_wrapper_1"  v-if="isShowSection_1">
          <!-- isShowSection_1 по умолчанию false. При прокрутке в методе showSection_1
          isShowSection_1 становится true и элемент появляется -->
          <picture id="start_page_section_1_picture">
            <source srcset="./images/main-background.png" media="(min-width: 1200px)" />
            <source srcset="./images/main-background.png" media="(min-width: 800px)" />
            <img src="./images/main-background.png"  alt="velo" />
          </picture>
          <div id="start_page_section_1_text">
            <p class="start_page_section_1_text_p">Лицей с углубленным изучением всего</p>
            <p class="start_page_section_1_text_p">Санкт-Петербург</p>
          </div>
        </div>
      </transition>
    </section>

    <section id="page_start_section_fixed_2"></section>

    <section id="start_page_section_2" v-on:scroll.passive="showSection_2">
      <transition name="slide-fade">
        <div id="start_page_section_wrapper_2" v-if="isShowSection_2">
          <!-- isShowSection_2 по умолчанию false. При прокрутке в методе showSection_2
          isShowSection_2 становится true и элемент появляется -->
          <img src="./images/mathematics-989121_1920.png" alt="pi">
          <div id="start_page_section_2_text">
            <p class="sstart_page_section_2_text_p">Инженерное направление</p>
          </div>
        </div>
      </transition>
    </section>

  </div>
  </div>
</template>

<script>
export default {
  name: 'HomeComp',
data() {
  return {
    isShowSection_1: false,
    isShowSection_2: false,
  }},
methods: {
  showSection_1: function () {
    let elem = document.getElementById('start_page_section_1');
    let position = elem.getBoundingClientRect();
    let y = position.top;
    let windowInnerHeight = document.documentElement.clientHeight;
    if(y < windowInnerHeight/3) {
      this.isShowSection_1 = true
    }
  },
  showSection_2: function () {
    let elem = document.getElementById('start_page_section_2');
    let position = elem.getBoundingClientRect();
    let y = position.top;
    let windowInnerHeight = document.documentElement.clientHeight;
    if (y < windowInnerHeight / 3) {
      this.isShowSection_2 = true
    }
  }
},
mounted: function(){
  window.addEventListener("scroll", this.showSection_1);
  window.addEventListener("scroll", this.showSection_2);
},
beforeRouteLeave: function(){
  window.removeEventListener("scroll", this.showSection_1);
  window.removeEventListener("scroll", this.showSection_2);
}

}
</script>

